import moment from 'moment'
import banner from "../assets/banner.jpeg"
import { Toast } from './helpers'

export const fromNowFilter = {
  filters: {
    fromNow(createdDate) {
      if (!createdDate) {
        return '-'
      }
      return moment(createdDate).fromNow()
    },
    fullDate(createdDate) {
      if (!createdDate) {
        return '-'
      }
      const year = moment(createdDate).format('YYYY')
      const month = moment(createdDate).format('MM')
      const date = moment(createdDate).format('DD')
      return year + '年' + month + '月' + date + '日'
    },
    getTime (createdDate)  {
      if (!createdDate) {
        return '-'
      }
      let period = moment(createdDate).format('a') === 'am' ? '早上' : '下午'
      const time = moment(createdDate).format('h:mm')
      return period + time
    }
  }
}
export const emptyImageFilter = {
  filters: {
    emptyImage(src){
      return src || 'data:image/png;base64,'
    },
    emptyBanner(src){
      return src || banner
    }
  }
}

export const accountFilter = {
  filters: {
    account(account){
      return account = '@' + account
    }
  }
}

export const alert = {
  warning(title){
    Toast.fire({
        icon: 'warning',
        title: title,
        background: '#FF6600',
        iconColor: '#fff',
        customClass: {
        container: 'sweetalert2-warning-pop',
      },
    })
  },
  error(title){
    Toast.fire({
      icon: 'error',
      title: title,
      background: '#FC5A5A',
      iconColor: '#fff',
      customClass: {
        container: 'sweetalert2-error-pop',
      },
    })
  },
  success(title){
    Toast.fire({
      icon: 'success',
      title: title,
    })
  }
}
